import { Component, Host, Prop, Element, h } from '@stencil/core';
import { TranslateService } from '../../../../services/translate.service';

@Component({
  tag: 'success-detail-modal',
  styleUrl: 'success-detail-modal.scss',
  shadow: true,
})
export class SuccessDetailModal {
  @Prop() job: any;
  @Prop() open: boolean;
  @Element() modal: any;
  public errorModalMaxHeight: number;
  public maxFailedOIDLength: number;

  private langTasks: any;
  public langCommon: any;

  componentWillLoad() {
    return Promise.resolve(true)
    // .then(() => {
    //   return TranslateService.getService().getTranslate();
    // })
    .then(() => {
      const res = TranslateService.getService().getTranslateSync();
      const langTasks = res.customPrint.recentTasks;
      this.langTasks = langTasks;
      this.langCommon = res.common;
    })
  }

  render() {
    return (
      <Host>
        <calcite-modal open={this.open} onCalciteModalClose={() => {this.open=false;}} aria-labelledby="modal-title" id="example-modal">
          <div slot="header" id="modal-title">
            {this.langTasks.detailSucceededOIDs}
            {this.job.resultInfo&&this.job.resultInfo.succeededObjectIds&&this.job.resultInfo.succeededObjectIds.length ? ' (' + this.job.resultInfo.succeededObjectIds.length + ') ' : '' }
          </div>
          <div slot="content">
            <div class="error-table-outter" style={{maxHeight: `${this.errorModalMaxHeight}`}}>
              { this.job&&this.job.resultInfo&&this.job.resultInfo.succeededObjectIds&&this.job.resultInfo.succeededObjectIds.length ?
               <table class="table table-striped table-bordered error-table">
                <tbody>
                  <tr>
                    <td>
                      <div class="message" style={{maxWidth:'720px'}}>
                        {this.job.resultInfo.succeededObjectIds}
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
              : ''
              }
            </div>
          </div>
          {/* <calcite-button slot="primary" width="full">
            {this.langCommon.ok}
          </calcite-button> */}
        </calcite-modal>

        <slot></slot>
      </Host>
    );
  }

}
